.grid {
  display: flex;
  align-items: flex-start;
  justify-content: center;
  flex-wrap: wrap;
}

.illustrators-grid {
  width: 71.8vw;
  transition: transform 800ms $easeOutExpo;

  &.blur,
  &:hover {
    .gallery-item {
      filter: grayscale(1) contrast(135%);
      opacity: 0.25;
    }
  }

  .gallery-item {
    margin: 0 4% 4% 0;
    transition: opacity 300ms $easeOutBack, filter 400ms $easeOutBack;

    &:hover {
      filter: grayscale(0);
      opacity: 1;
    }
  }

  .illustrator-meta-label {
    margin-bottom: 0;
    font-size: 0.857em;
  }

  .illustrator-name {
    color: $site-font-color;
  }

  .bricklayer-column:last-child {
    .gallery-item {
      margin-right: 0;
    }
  }
}

@-moz-document url-prefix() {
  .illustrators-grid {
    will-change: transform;
  }
}

.gallery-grid {
  margin-top: 1%;

  .bricklayer-column-sizer {
    width: 50%;

    @media screen and (min-width: $large-vp) {
      width: 33.3333333333%;
    }
  }

  @media screen and (max-width: $small-vp) {
    .bricklayer-column:last-child {
      .gallery-item {
        margin-right: 0;
      }
    }
  }
}

.bricklayer-column-sizer {
  display: none;
  width: 33.333%;

  @media screen and (max-width: $small-vp) {
    width: 50%;
  }

  @media screen and (max-width: $smallest-vp) {
    width: 100%;
  }

  @media screen and (min-width: $medium-vp) {
    width: 25%;
  }

  @media screen and (min-width: $large-vp) {
    width: 16.6666667%;
  }
}

.bricklayer-column {
  flex: 1;
}

// Touchevent Cases

.touchevents {

  .illustrators-grid {

    &.blur,
    &:hover {

      .gallery-item {
        filter: grayscale(0);
        opacity: 1;
      }
    }
  }
}



